import React, { Component } from 'react'
import NumbersAPI from "../../Component/NumbersAPI/NApi"
import CompoundInterest from '../../Component/calculatorSecond/calculatorsecond'
import SecondAPI from '../../Component/SecondAPI/SecondAPI'
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs'
import Testimonials from '../../Component/Testimonials/Testimonials'
import NPSMain from '../Home/images/National Pension Scheme/1.png'
import NPSF1 from '../Home/images/National Pension Scheme/Voluntary Scheme.svg'
import NPSF2 from '../Home/images/National Pension Scheme/Low-Cost Scheme.svg'
import NPSF3 from '../Home/images/National Pension Scheme/Portability.svg'
import NPSF4 from '../Home/images/National Pension Scheme/Portability.svg'
import { withTranslation } from 'react-i18next';
import {Helmet} from 'react-helmet';


export class NPS extends Component {
   constructor() {
      super()
   }

   data = {
      "NPS": {
         "Title": "",
         "ProductCategoryId": "6",
         "SebiCategory": "",
         "SebiSubCategory": "",
         "DefaultProductId": "1",
      }
   }

   render() {
      const productinfo = this.data.NPS;
      const{t} =this.props;


      return (
         <main>
            <Helmet>
                <meta charSet="utf-8" />
                <title>{t('MetaTitle:mT52')}</title>
                <meta name="description" content={t('MetaDescription:mD52')} />
            </Helmet>


            <div className="container container-wrapper">
               <div className="row">
                  <div className="col-lg-7 col-md-6 mb-4">
                     <h1>{t('NPS:nPS1')}</h1>
                     <p>{t('NPS:nPS2')}</p>
                     <div className="registerFair">
                        <a href="https://app.nivesh.com/home/index"><button type="button" className="btn btn-primary btn-lg">{t('CTAFooter:InvestNow1')}</button></a>
                     </div>
                  </div>
                  <div className="col-lg-5 col-md-6"><img src={NPSMain} className='img-fluid' /></div>
               </div>
            </div>

            <div className="API-Section">
               <div className="container container-wrapper">
                  <h2 className='text-center'>Best NPS Plans</h2>
                  <div className='row row justify-content-lg-center'>
                     <div className='col col-lg-10'>
                        <SecondAPI productCategoryId={productinfo.ProductCategoryId} sebiCategory={productinfo.SebiCategory} sebiSubCategory={productinfo.SebiSubCategory} defaultProductId={productinfo.DefaultProductId} />
                     </div>
                  </div>
               </div>
            </div>

            <div className="bg-white">
               <div className="container features">
                  <div className="row">
                     <div className="col-lg-12">
                        <h2>{t('NPS:nPS3')}</h2>
                     </div>
                     <div className="col-lg-3 col-md-6">
                        <div className="imgFeatures">
                           <img src={NPSF1} />
                           <h4>{t('NPS:nPS4')}</h4>
                           <p className='small-text'>{t('NPS:nPS5')}</p>
                        </div>
                     </div>
                     <div className="col-lg-3 col-md-6">
                        <div className="imgFeatures">
                           <img src={NPSF2} />
                           <h4>{t('NPS:nPS6')}</h4>
                           <p className='small-text'>{t('NPS:nPS7')}</p>
                        </div>
                     </div>
                     <div className="col-lg-3 col-md-6">
                        <div className="imgFeatures">
                           <img src={NPSF3} />
                           <h4>{t('NPS:nPS8')}</h4>
                           <p className='small-text'>{t('NPS:nPS9')}</p>
                        </div>
                     </div>
                     <div className="col-lg-3 col-md-6">
                        <div className="imgFeatures">
                           <img src={NPSF4} />
                           <h4>{t('NPS:nPS10')}</h4>
                           <p className='small-text'>{t('NPS:nPS11')}</p>
                        </div>
                     </div>
                  </div>
                  <div className="text-center"><a href="https://app.nivesh.com/home/index" className='btn btn-primary'>{t('CTAFooter:InvestNow1')}</a></div>
               </div>
            </div>

            <div className="getStarted">
               <div className="container">
                  <div className="row">
                     <div className="col-lg-5 getStartedBtn">
                        <h2>{t('NPS:nPS12')}</h2>
                        <p>{t('NPS:nPS13')}</p>
                        <div className="text-left"><a href="https://app.nivesh.com/home/index" className='btn btn-primary'>{t('CTAFooter:InvestNow1')}</a></div>
                     </div>
                     <div className="col-lg-7">
                        <div className="stepWrp">
                           <div className="stepLft"> Step #1:</div>
                           <div className="stepRgt">{t('NPS:nPS14')}</div>
                        </div>
                        <div className="stepWrp">
                           <div className="stepLft"> Step #2:</div>
                           <div className="stepRgt">{t('NPS:nPS15')}</div>
                        </div>
                        <div className="stepWrp">
                           <div className="stepLft"> Step #3:</div>
                           <div className="stepRgt">{t('NPS:nPS16')}</div>
                        </div>
                        <div className="stepWrp">
                           <div className="stepLft"> Step #4:</div>
                           <div className="stepRgt">{t('NPS:nPS17')}</div>
                        </div>
                     </div>
                  </div>
               </div>
            </div>

            <div className="container container-wrapper">
               <h2>{t('NPS:nPS18')}</h2>
               <Tabs>
                  <TabList>
                     <Tab>{t('NPS:nPS19')}</Tab>
                     <Tab>{t('NPS:nPS32')}</Tab>
                     <Tab>{t('NPS:nPS37')}</Tab>
                  </TabList>

                  <TabPanel>
                     <ul>
                        <li>{t('NPS:nPS20')}</li>
                        <li>{t('NPS:nPS21')}</li>
                     </ul>
                     <p>There are two sub-accounts available while opening an NPS funds in India:</p>
                     <ul>
                        <li><b>{t('NPS:nPS22')}</b></li>
                        {t('NPS:nPS23')}<br />
                        {t('NPS:nPS24')}<br />
                        {t('NPS:nPS25')}<br />
                        {t('NPS:nPS26')}<br />
                        <li><b>{t('NPS:nPS27')}</b></li>
                        {t('NPS:nPS28')}<br />
                        {t('NPS:nPS29')}<br />
                        {t('NPS:nPS30')}<br />
                        {t('NPS:nPS31')}

                     </ul>

                  </TabPanel>
                  <TabPanel>
                     <p>{t('NPS:nPS33')}</p>

                     <ul>
                     <li>{t('NPS:nPS35')}</li>
                     </ul>
                     <p>{t('NPS:nPS36')}</p>
                  </TabPanel>
                  <TabPanel>
                     <p>{t('NPS:nPS38')}</p>
                     <ul>
                     <li>{t('NPS:nPS39')}</li>
                     </ul>
                  </TabPanel>
               </Tabs>
            </div>

            <NumbersAPI />


            <div className='container container-wrapper'>
               <h2>{t('NPS:nPS40')}</h2>
               <p>
                  <ul>
                     <li>{t('NPS:nPS41')}</li>
                     <li>{t('NPS:nPS42')}</li>
                     <li>{t('NPS:nPS43')}</li>
                     <li>{t('NPS:nPS44')}</li>
                  </ul>
               </p>

            </div>

            <div className="landingPlatforms">
               <div className="container">
                  <div className="row">
                     <h2>{t('NPS:nPS45')}</h2>
                     <ul className='ul-white'>
                        <li>{t('NPS:nPS46')}</li>
                        <li>{t('NPS:nPS47')}</li>
                        <li>{t('NPS:nPS48')}</li>
                        <li>{t('NPS:nPS49')}</li>
                        <li>{t('NPS:nPS50')}</li>
                     </ul>

                  </div>
               </div>
            </div>

            <div className="calCulator">
            <div className="container container-wrapper">
                <h2 className='mb-4'>NPS Calculator</h2>
            <CompoundInterest />
            </div>
            </div>
            <Testimonials />


            <div className="container testimonials">
               <div className="row">
                  <div className="col-lg-12">
                     <h2>{t('NPS:nPS51')}</h2>
                     <div id="accordion">
                        <h3>{t('NPS:nPS52')}</h3>
                        <p>{t('NPS:nPS53')}</p>
                        <h3>{t('NPS:nPS54')}</h3>
                        <p>{t('NPS:nPS55')}</p>

                        <h3>{t('NPS:nPS56')}</h3>
                        <p>{t('NPS:nPS57')}</p>

                        <h3>{t('NPS:nPS58')}</h3>
                        <p>{t('NPS:nPS59')}</p>
                        <h3>{t('NPS:nPS60')}</h3>
                        <ul>
                           <li>{t('NPS:nPS61')}</li>
                           <li>{t('NPS:nPS62')}</li>
                           <li>{t('NPS:nPS64')}</li>
                        </ul>
                     </div>
                  </div>
               </div>

            </div>
         </main>
      );
   }
}

export default withTranslation() (NPS)
